.keys {
    position: fixed;
    left: 0;
    width:100%;
    font-family: Sans-Serif;
    font-weight: 100;
    border-radius: 3px;
    margin: 2em 0 0 0 !important;
    padding: 0;
    clear:both;
    -webkit-user-select: none;
       -moz-user-select: none;
        -ms-user-select: none;
            user-select: none;
    bottom: 20px;
    -webkit-transform:translateY(0px);
        -ms-transform:translateY(0px);
            transform:translateY(0px);
    background-color: rgba(25,25,25, .90);
    border-top: 1px solid var(--gray);
    -webkit-box-shadow: 0px 30px 30px 30px var(--bg-color);
            box-shadow: 0px 30px 30px 30px var(--bg-color);
    -webkit-transition: all 140ms ease-out;
    -o-transition: all 140ms ease-out;
    transition: all 140ms ease-out;
    z-index: 1000;
}


.keys .keyboard {
  display: block;
  width: 994px;
  margin: 0 auto;
}

.keys.disabled{
  -webkit-transform:translateY(300px);
      -ms-transform:translateY(300px);
          transform:translateY(300px);
  overflow: hidden
}

.keys .keyboard:after,
.keys:after {
    display: table;
    content: "";
    clear: both;
}

.keys .keyboard > div {
    display: block;
    float: left;
    width: 40px;
    height: 140px;
    margin: 20px 3.5px;
    text-align: center;
    border: 1px solid var(--gray);
    line-height: 240px;
    border-radius: 3px;
    background-color: var(--white);
    color: var(--bg-color);
}

.keys .keyboard > div[class*='♭'] {
    background: var(--gray);
    margin-top: 10px;
    color: var(--white);
}

.keys .key sub {
    font-size: 0.7em;
    margin: 0.15em;
}

.keys .key:hover,
.keys .key:active{
    background: var(--red);
}

.octave{
  display: block;
  width: 130px;
  margin: 0 auto;
  text-align: center;
}

.octave:before{
  content: '';
  display: table;
}

.octave:after{
  content: '';
  display: table;
  clear: both;
}

.octave input {
  display: inline-block;
}

.toggle-keyboard {
  background-color: var(--gray);
  background-image: url('../../images/piano_icon.svg');
  background-repeat: no-repeat;
  background-position: 50.6% 50%;
  width: 29px;
  height: 29px;
  cursor:pointer;
}
.keys .toggle-keyboard {
  background-image: url('../../images/keyboard_close.svg');
}

.keys .toggle-keyboard{
  position: absolute;
  display: block;
  top: 1em;
  right: 1em;
}


