.toolbar {
  width: calc(99.9% * 12/12 - (30px - 30px * 12/12));
  margin: 0;
}.toolbar:nth-child(1n) {
  float: left;
  margin-right: 30px;
  clear: none;
}.toolbar:last-child {
  margin-right: 0;
}.toolbar:nth-child(12n) {
  margin-right: 0;
  float: right;
}.toolbar:nth-child(12n + 1) {
  clear: both;
}.toolbar:before {
  content: '';
  display: table;
}.toolbar:after {
  content: '';
  display: table;
  clear: both;
}

.toolbar button{
  font-weight: bold;
  width: 29px;
  height: 29px;
  line-height: 1em;
}

.toolbar .logo-holder {
  width: calc(99.9% * 4.5/15 - (30px - 30px * 4.5/15));
  position:relative;
}

.toolbar .logo-holder:nth-child(1n) {
  float: left;
  margin-right: 30px;
  clear: none;
}

.toolbar .logo-holder:last-child {
  margin-right: 0;
}

.toolbar .logo-holder:nth-child(15n) {
  margin-right: 0;
  float: right;
}

.toolbar .logo-holder:nth-child(15n + 1) {
  clear: both;
}

.toolbar .logo-holder:before {
  content: '';
  display: table;
}

.toolbar .logo-holder:after {
  content: '';
  display: table;
  clear: both;
}

.toolbar .logo-holder .logo {
  position: absolute;
  top: 3px;
  left: 50%;
  -webkit-transform: translateX(-25.5%);
      -ms-transform: translateX(-25.5%);
          transform: translateX(-25.5%);
  display: block;
  height: 100px;
  width: 112px;
  background-image: url("../../images/quirki_m3_logo.svg");
  background-size: contain;
  background-repeat: no-repeat;
  z-index: 3;
}

.toolbar .presets {
  width: calc(99.9% * 3.6/15 - (30px - 30px * 3.6/15));
  line-height: 20px;
  text-align:center;
  margin-right: 0px !important;
  margin-left: 15px;
  position:relative;
}

.toolbar .presets:nth-child(1n) {
  float: left;
  margin-right: 30px;
  clear: none;
}

.toolbar .presets:last-child {
  margin-right: 0;
}

.toolbar .presets:nth-child(15n) {
  margin-right: 0;
  float: right;
}

.toolbar .presets:nth-child(15n + 1) {
  clear: both;
}

.toolbar .presets .new-preset-name {
  width: calc(99.9% * 12/12 - (30px - 30px * 12/12));
  height: 65px;
  text-align: center;
  position: absolute;
  top: 0px;
  left: 0px;
  background-color: var(--bg-color);
  background-image: -webkit-gradient(linear,left top, left bottom,from(#191919),color-stop(220%, #242424));
  background-image: -webkit-linear-gradient(#191919 0%,#242424 220%);
  background-image: -o-linear-gradient(#191919 0%,#242424 220%);
  background-image: linear-gradient(#191919 0%,#242424 220%);
  border-radius: 3px;
  -webkit-transform: translateY(-130px);
      -ms-transform: translateY(-130px);
          transform: translateY(-130px);
  -webkit-transition: all 100ms ease-out;
  -o-transition: all 100ms ease-out;
  transition: all 100ms ease-out;
}

.toolbar .presets .new-preset-name:nth-child(1n) {
  float: left;
  margin-right: 30px;
  clear: none;
}

.toolbar .presets .new-preset-name:last-child {
  margin-right: 0;
}

.toolbar .presets .new-preset-name:nth-child(12n) {
  margin-right: 0;
  float: right;
}

.toolbar .presets .new-preset-name:nth-child(12n + 1) {
  clear: both;
}

.toolbar .presets .new-preset-name.visible{
  -webkit-transform: translateY(0px);
      -ms-transform: translateY(0px);
          transform: translateY(0px);
  -webkit-transition: all 100ms ease-out;
  -o-transition: all 100ms ease-out;
  transition: all 100ms ease-out;
}

.toolbar .presets .new-preset-name input{
  display: block;
  width: 90%;
  margin: 0 auto 4px auto;
  padding: 4px 3px;
  border: 1px solid var(--gray);
}

.toolbar .presets .new-preset-name button{
  width: auto;
  height: auto;
  padding: 0.85em;
  font-weight: bold;
}

.midi {
  width:calc(99.9% * 3.85/15 - (30px - 30px * 3.85/15));
  text-align:center;
  position: relative;
  margin-right: 0px !important;
  margin-left: 15px;
}

.midi:nth-child(1n) {
  float:left;
  margin-right:30px;
  clear:none;
}

.midi:last-child {
  margin-right:0;
}

.midi:nth-child(15n) {
  margin-right:0;
  float:right;
}

.midi:nth-child(15n + 1) {
  clear:both;
}

.midi .toggle-keyboard {
  display:inline-block;
}

.master-volume{
  width: calc(99.9% * 3.3/15 - (30px - 30px * 3.3/15));
  margin: 0 !important;
  line-height: 20px;
  text-align:center;
}

.master-volume:nth-child(1n){
  float: left;
  margin-right: 30px;
  clear: none;
}

.master-volume:last-child{
  margin-right: 0;
}

.master-volume:nth-child(15n){
  margin-right: 0;
  float: right;
}

.master-volume:nth-child(15n + 1){
  clear: both;
}

.toolbar label {
  display: block !important;
  width: 100%;
  clear: both;
  margin-top: 10px;
}

.master-volume .volume {
  display: inline;
  width: 62%;
  margin: 2% 2% 2% 8%;
}

.master-volume button {
  display: inline-block;
  background-image: url('../../images/speaker_icon.svg');
  background-position: 50.6% 50%;
  background-repeat: no-repeat;
  margin: 0;
  -webkit-transform: translateY(-9px);
      -ms-transform: translateY(-9px);
          transform: translateY(-9px);
}



