.lfos {
    width: calc(99.9% * 4/12 - (30px - 30px * 4/12));
    margin-top: 4px !important;
}.lfos:nth-child(1n) {
    float: left;
    margin-right: 30px;
    clear: none;
}.lfos:last-child {
    margin-right: 0;
}.lfos:nth-child(12n) {
    margin-right: 0;
    float: right;
}.lfos:nth-child(12n + 1) {
    clear: both;
}

.lfo{
    max-width: calc(99.9% * 11/12);
    margin-left: auto;
    margin-right: auto;
    position: relative;
    margin-top: 1.75em;
}

.lfo:before{
    content: '';
    display: table;
}

.lfo:after{
    content: '';
    display: table;
    clear: both;
}

.lfo-osctype-labels{
    width: calc(99.9% * 12/12 - (30px - 30px * 12/12));
    font-family: Sans-Serif;
    padding: 0;
    margin: 5px 0 1.5em;
    background-color: var(--gray);
    border: 3px solid var(--gray);
    color: var(--white);
}

.lfo-osctype-labels:nth-child(1n){
    float: left;
    margin-right: 30px;
    clear: none;
}

.lfo-osctype-labels:last-child{
    margin-right: 0;
}

.lfo-osctype-labels:nth-child(12n){
    margin-right: 0;
    float: right;
}

.lfo-osctype-labels:nth-child(12n + 1){
    clear: both;
}

.lfo-osctype-labels label{
    width: 24%;
    float:left;
    list-style: none;
    margin: 0;
    padding: 2% 1% 2% 0;
    height: 2em;
    line-height: 2em;
    text-align: center;
    clear: none;
}

.lfo-osctype-labels label:nth-child(2n+2) {
    background-color: var(--bg-color);
    color: var(--white);
}

.lfo-osctype,
.lfo-rate {
    width: calc(99.9% * 12/12 - (30px - 30px * 12/12));
}

.lfo-osctype:nth-child(1n),
.lfo-rate:nth-child(1n) {
    float: left;
    margin-right: 30px;
    clear: none;
}

.lfo-osctype:last-child,
.lfo-rate:last-child {
    margin-right: 0;
}

.lfo-osctype:nth-child(12n),
.lfo-rate:nth-child(12n) {
    margin-right: 0;
    float: right;
}

.lfo-osctype:nth-child(12n + 1),
.lfo-rate:nth-child(12n + 1) {
    clear: both;
}

.lfo-osctype:before,
.lfo-rate:before {
    content: '';
    display: table;
}

.lfo-osctype:after,
.lfo-rate:after {
    content: '';
    display: table;
    clear: both;
}

.lfo-osctype{
  height: 50px;
  position: absolute;
  top: 50px;
  margin-top: 25px;
  border: 0px !important;
}


.lfo-osctype[type=range] {
  left: 1px;
}
.lfo-osctype[type=range]::-webkit-slider-runnable-track {
  height: 3px;
  -webkit-box-shadow: none;
          box-shadow: none;
}

.lfo-osctype[type=range]::-webkit-slider-thumb {
   -webkit-transform: translateY(-5px);
           transform: translateY(-5px);
   border: 1px solid var(--red);
}

.lfo-osctype[type=range]:focus::-webkit-slider-thumb {
   border: 1px solid var(--translucent-white);
}


