.oscillators {
  width: calc(99.9% * 12/12 - (30px - 30px * 12/12));
  margin-bottom: 60px;
}.oscillators:nth-child(1n) {
  float: left;
  margin-right: 30px;
  clear: none;
}.oscillators:last-child {
  margin-right: 0;
}.oscillators:nth-child(12n) {
  margin-right: 0;
  float: right;
}.oscillators:nth-child(12n + 1) {
  clear: both;
}.oscillators:before {
  content: '';
  display: table;
}.oscillators:after {
  content: '';
  display: table;
  clear: both;
}

.keyboard-enabled .oscillators {
  margin-bottom: 330px;
}

.vco-holder{
    width: calc(99.9% * 4/12 - (30px - 30px * 4/12));
    margin: 0 5px 0 15px !important;
}

.vco-holder:nth-child(1n){
    float: left;
    margin-right: 30px;
    clear: none;
}

.vco-holder:last-child{
    margin-right: 0;
}

.vco-holder:nth-child(12n){
    margin-right: 0;
    float: right;
}

.vco-holder:nth-child(12n + 1){
    clear: both;
}

.vco-holder:nth-child(3){
  margin: 0 0 0 15px !important;
}

.vco{
    max-width: 11.4/12;
    margin-left: auto;
    margin-right: auto;
    padding-top: 1em;
}

.vco:before{
    content: '';
    display: table;
}

.vco:after{
    content: '';
    display: table;
    clear: both;
}

.buttons-knobs{
    width: calc(99.9% * 2/12 - (0px - 0px * 2/12));
}

.buttons-knobs:nth-child(1n){
    float: left;
    margin-right: 0px;
    clear: none;
}

.buttons-knobs:last-child{
    margin-right: 0;
}

.buttons-knobs:nth-child(0n){
    float: right;
}

.buttons-knobs button{
  width:100%;
}

.osctype-selector{
    width: calc(99.9% * 4.2/12 - (30px - 30px * 4.2/12));
    position:relative;
    overflow:hidden;
    margin: 3.2em 4px 0 16px!important ;
}

.osctype-selector:nth-child(1n){
    float: left;
    margin-right: 30px;
    clear: none;
}

.osctype-selector:last-child{
    margin-right: 0;
}

.osctype-selector:nth-child(12n){
    margin-right: 0;
    float: right;
}

.osctype-selector:nth-child(12n + 1){
    clear: both;
}

.osctype {
    position:absolute;
    top:16px;
    left:-21px;
    width: 210px;
    height: 75px;
    -webkit-transform: rotate(-90deg) translateX(-232px);
        -ms-transform: rotate(-90deg) translateX(-232px);
            transform: rotate(-90deg) translateX(-232px);
    -webkit-transform-origin: 0 0;
        -ms-transform-origin: 0 0;
            transform-origin: 0 0;
    outline: none;
    overflow: visible;
    z-index: 0;
    border: 0px !important;
    padding: 0 10px 30px 30px;
}

.osctype:focus {
    outline: none;
    border: 0px !important;
}

.osctype-labels {
    width: 60px;
    margin: 0 0 0 1.6em;
    padding: 0;
    font-size: 12px;
    list-style: none;
    border-top: 3px solid var(--gray);
    border-left: 3px solid var(--gray);
    border-right: 3px solid var(--gray);
    font-family: Sans-Serif;
    z-index: 1;
}

.osctype-labels li {
    margin: 0;
    padding: 0 0.5em 0 0.60em;
    height: 5.2em;
    line-height: 5.2em;
    text-align: center;
    cursor: pointer;
}

.osctype-labels li:nth-child(2n+2) {
    background-color: var(--gray);
    color: var(--white);
}

.osc-volumes {
    width: calc(99.9% * 11.5/12 - (30px - 30px * 11.5/12));
}

.osc-volumes:nth-child(1n) {
    float: left;
    margin-right: 30px;
    clear: none;
}

.osc-volumes:last-child {
    margin-right: 0;
}

.osc-volumes:nth-child(12n) {
    margin-right: 0;
    float: right;
}

.osc-volumes:nth-child(12n + 1) {
    clear: both;
}

.osc-volumes:before {
    content: '';
    display: table;
}

.osc-volumes:after {
    content: '';
    display: table;
    clear: both;
}

.osc-volumes h2 {
  margin-top: 0;
}

.osc-volumes input {
  width: 100%;
}

.osctype-selector input[type=range]::-webkit-slider-thumb {
  -webkit-transform: translateY(-9px);
          transform: translateY(-9px);
  border: 1px solid var(--red);
  height: 19px;
}

.osctype-selector input[type=range]:focus::-webkit-slider-thumb {
  border: 1px solid var(--translucent-white);
}

.osctype-selector input[type=range]::-webkit-slider-runnable-track {
  height: 3px;
  -webkit-box-shadow: none;
          box-shadow: none;
  background: rgba(0,0,0,0);
  left: -5px;
  border: 0px !important;
  outline: none;
}

.adsr{
    width: calc(99.9% * 6/12 - (30px - 30px * 6/12));
    padding: 0;
    margin: 0 !important;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
}

.adsr:nth-child(1n){
    float: left;
    margin-right: 30px;
    clear: none;
}

.adsr:last-child{
    margin-right: 0;
}

.adsr:nth-child(12n){
    margin-right: 0;
    float: right;
}

.adsr:nth-child(12n + 1){
    clear: both;
}

.adsr input {
  width: 100%;
}

.adsr .envelope canvas {
  margin: 1em auto !important;
}
.oscpitch {
  -webkit-appearance: slider-vertical;
     -moz-appearance: slider-vertical;
          appearance: slider-vertical;
}


