:root {
  --white: #cecece;
  --translucent-white: rgba(216,216,216, 0.35);
  --bg-color: #191919;
  --translucent-bg-color: rgba(25,25,25, 0.9);
  --gray: #444;
  --red: #B22C2C;
}

@import url('https://fonts.googleapis.com/css?family=PT+Sans');
.app {
    max-width: calc(99.9% * 12/12);
    margin-left: auto;
    margin-right: auto;
    padding-left: 0;
    padding-right: 0;
    min-width: 1024px;
    max-width: 1600px;
    margin-top: 24px;
}
.app:before {
    content: '';
    display: table;
}
.app:after {
    content: '';
    display: table;
    clear: both;
}

.titlebar {
  display: block;
  width: 100%;
  height: 26px;
  background-color: rgba(25,25,25,0.9);
  -webkit-box-shadow: 0px 0px 4px 0 #191919;
          box-shadow: 0px 0px 4px 0 #191919;
  position: fixed;
  top: 0;
  left: 0;
  z-index: 1000;
}

.shaping {
  width: calc(99.9% * 12/12 - (30px - 30px * 12/12));
  margin-bottom: 2em;
}

.shaping:nth-child(1n) {
  float: left;
  margin-right: 30px;
  clear: none;
}

.shaping:last-child {
  margin-right: 0;
}

.shaping:nth-child(12n) {
  margin-right: 0;
  float: right;
}

.shaping:nth-child(12n + 1) {
  clear: both;
}

.shaping:before {
  content: '';
  display: table;
}

.shaping:after {
  content: '';
  display: table;
  clear: both;
}

html {
  display: block;
  margin: 0;
  padding: 0;
  width: 100%;
  height: 100%;
  overflow: hidden;
  position: relative;
}

body {
  position: absolute;
  top: 0;
  left: 0;
  display: block;
  margin: 0;
  padding: 0;
  background-color: var(--bg-color);
  background-image: -webkit-radial-gradient(#242424 0%, #191919 90%);
  background-image: -o-radial-gradient(#242424 0%, #191919 90%);
  background-image: radial-gradient(#242424 0%, #191919 90%);
  color: var(--white);
  font-family: "PT Sans";
  letter-spacing: 0.034em;
  width: 100%;
  height: 100%;
  overflow: scroll;
  -webkit-overflow-scrolling: touch;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
  cursor: default;
  font-size: 1.03em;
  -webkit-app-region: drag;
}

canvas {
  -webkit-transform: translateZ(0);
          transform: translateZ(0);
}

label {
  font-size: 0.8em;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
  cursor: default;
  padding: 0;
  display: block;
  clear: both;
  margin: 1em 0 0.25em;
}

select {
  -webkit-appearance: none;
     -moz-appearance: none;
          appearance: none;
  background: url(data:image/svg+xml;base64,);
  background-repeat: no-repeat;
  background-position: right 8px center;
  background-color: transparent;
  outline: none;
  padding:0.5em 25px 0.5em 0.5em;
  border: 1px solid var(--translucent-bg-color);
  border-bottom: 1px solid var(--gray);
  color: var(--white);
  cursor: pointer;
  border-radius: 0px;
  margin-right: 5px;
}


select:focus,
select:hover {
  background-color: var(--gray);
  border: 1px solid var(--translucent-white);
  border-radius: 2px;
}

h2 {
  font-size: 1em;
}

input[type=range] {
  -webkit-appearance: none;
     -moz-appearance: none;
          appearance: none;
  background: none;
  cursor:pointer;
}

input[type=range]:focus {
  outline: none;
}

input[type=range]::-webkit-slider-runnable-track {
  width: 100%;
  height: 24px;
  background: var(--gray);
  border-radius: 5px;
  border: 1px solid var(--bg-color);
}

input[type=range]:focus::-webkit-slider-runnable-track {
  border: 1px solid var(--translucent-white);
}

input[type=range]::-webkit-slider-thumb {
  height: 24px;
  width: 20px;
  border-radius: 2px;
  background: var(--red);
  -webkit-appearance: none;
          appearance: none;
}

button {
    background-color:var(--gray);
    color: var(--white);
    border-radius: 2px;
    margin: 0.25em;
    padding: 0.8em 0.5em;
    outline:none;
    cursor: pointer;
    border-radius: 3px;
    border: 1px solid var(--bg-color);
}

button:focus {
  border: 1px solid var(--translucent-white);
}

button.on{
    background-color:var(--red);
    outline:none;
}

input,
button,
canvas,
textarea,
.keys {
  -webkit-app-region: no-drag;
}



