.pitch-control {
  margin: 2em 0 0;
}

.pitch-control h2 {
  margin-bottom:0;
}

.pitchOffset {
  display:block;
  float:left;
  clear:both;
  background: #000;
  margin: 0;
  padding: 0.5em 0;
  width: 100%;
  text-align: center;
  color: var(--red);
  border: 2px solid var(--gray);
  line-height: 2em;
}

.pitch-control button{
  display: block;
  float: left;
  clear:both;
  width: 100%;
  margin: 0.25em 0 0.25em .125em;
  font-weight: bold;
  font-size: 1.05em;
  padding: 2px 0;
}


