.oscilloscope-holder {
  max-width: calc(99.9% * 11/12);
  margin-left: auto;
  margin-right: auto;
}.oscilloscope-holder:before {
  content: '';
  display: table;
}.oscilloscope-holder:after {
  content: '';
  display: table;
  clear: both;
}

.oscilloscope {
  display: block;
  width: 100%;
  height: 297px;
  margin: 0;
  border: 2px solid var(--gray);
}

.oscilloscope:before {
  content: '';
  display: table;
}

.oscilloscope:after {
  content: '';
  display: table;
  clear: both;
}



